body {
  background-color: #fff;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #646464;
}

.btn {
  background-color: #37806b;
  padding: 10px 20px;
  border-radius: 20px 0;
  transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.btn:hover, .btn:focus {
  background-color: #caa892;
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  padding: 0;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.container {
  max-width: 760px;
  padding-left: 25px;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(255, 192, 192);
}
@media screen and (min-width: 1060px) and (max-width: 1439px) {
  .container {
    max-width: 1060px;
    padding-left: 25px;
    padding-right: 10px;
    background-color: rgb(154, 172, 255);
  }
}
@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 64px;
    padding-right: 54px;
    background-color: rgb(212, 255, 223);
  }
}

